/* Components
-------------------------------------------*/
.skip-link {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 100;
  background: #f8f9fa;
  color: #000;
  display: block;
  padding: 10px 20px;
  text-align: center;
  -webkit-transform: translate(-50%, -200%);
  transform: translate(-50%, -200%);
  -webkit-transition: -webkit-transform .35s;
  transition: -webkit-transform .35s;
  transition: transform .35s;
  transition: transform .35s,-webkit-transform .35s; }
  .skip-link:focus, .skip-link:active {
    -webkit-transform: translate(-50%, 25px);
    transform: translate(-50%, 25px); }

.navbar-light .navbar-text a {
  color: white; }
  .navbar-light .navbar-text a:focus, .navbar-light .navbar-text a:hover {
    color: white; }

.navbar-brand__logo {
  max-width: 200px; }
  @media (min-width: 576px) {
    .navbar-brand__logo {
      max-width: 280px; } }

.navbar-expand-lg .navbar-nav .dropdown-menu {
  z-index: 9999; }

/* ---------------------------------------------------------------
Subnavigation
--------------------------------------------------------------- */
.sidebar {
  padding: 1rem;
  background: #fff;
  position: relative;
  border-left: 5px solid #4571b6; }
  .sidebar__title {
    font-size: 12px;
    background: #4571b6;
    display: inline-block;
    color: #000;
    margin: 0;
    padding: 8px 14px;
    line-height: 1;
    position: absolute;
    top: 0;
    left: 1.5rem;
    font-family: Roboto, sans-serif;
    text-transform: uppercase;
    letter-spacing: 1px; }
    .sidebar__title a {
      color: #000; }
  .sidebar__list {
    list-style: none;
    margin: 0;
    padding: 0; }
    .sidebar__list--sub {
      background-color: #f8f9fa; }
  .sidebar__link {
    border-bottom: 1px solid #e9ecef;
    display: block;
    color: #495057;
    font-size: 15px;
    padding: 12px 6px 12px; }
    .sidebar__link--current {
      background-color: #f8f9fa; }
    .sidebar__link--sub {
      text-transform: none;
      font-size: 13px;
      line-height: 1.4; }
    .sidebar__link:hover, .sidebar__link:focus {
      color: #343a40;
      text-decoration: underline; }

.btn-stream-twitch {
  background: #6441a5;
  border: none; }
  .btn-stream-twitch:hover, .btn-stream-twitch:active, .btn-stream-twitch:focus {
    background: #6441a5; }

.text-divider {
  margin: 2em auto;
  display: flex;
  text-align: center;
  align-items: center; }
  .text-divider span {
    font-size: 14px;
    letter-spacing: 0.1em;
    white-space: nowrap;
    padding: 0 1em;
    font-style: italic; }
  .text-divider::before, .text-divider::after {
    content: '';
    display: inline-block;
    height: 1px;
    width: 20px;
    flex-grow: 1;
    background: #ced4da;
    vertical-align: middle; }
  @media (min-width: 768px) {
    .text-divider {
      margin: 3em auto; } }

.link-highlight {
  background-repeat: no-repeat;
  background-image: linear-gradient(transparent 50%, rgba(42, 109, 244, 0.2) 50%);
  background-image: linear-gradient(transparent 50%, #ffb99d 50%);
  background-size: 0% 100%;
  will-change: background-size;
  transition: background-size 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: background-size .3s ease-in-out; }
  .link-highlight:hover, .link-highlight:focus {
    background-size: 100% 100%; }

/* Layout
-------------------------------------------*/
#BetterNavigator {
  display: none; }

.show-title {
  position: relative;
  z-index: 1; }
  .show-title a {
    background-color: #f8f9fa; }
