.tickets {
  position: relative; }
  .tickets p {
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.4); }
  .tickets::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: url(../images/IOWA-ParticlePattern.png) 0 0 repeat;
    opacity: .15; }

